@import './font-awesome.css';
@import './iphone.css';
@import './profilling.css';
@import './demo-gen.css';
@import '~highlight.js/styles/atom-one-dark.css';
@import '~react-quill/dist/quill.snow.css';

.nav-link {
    cursor: pointer;
}

;
body {
    background: -webkit-linear-gradient(135deg, #F2C14E, #F78154, #54caf7, #5477f7) !important;
    background: -o-linear-gradient(135deg, #F2C14E, #F78154, #54caf7, #5477f7) !important;
    background: linear-gradient(-45deg, #F2C14E, #F78154, #54caf7, #5477f7) !important;
    background-size: 400% 400%;
    -webkit-animation: Gradient 15s ease infinite;
    animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient {
    0% {
        background-position: 0% 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0% 50%
    }
}

@keyframes Gradient {
    0% {
        background-position: 0% 50%
    }
    50% {
        background-position: 100% 50%
    }
    100% {
        background-position: 0% 50%
    }
}

.tr_selected {
    cursor: pointer;
}

.tr_selected:hover,
.tr_selected.active {
    background-color: #f9f9f9;
}

.fa-times {
    color: #f44336;
}

.questionDiv img{
    width: 100%;
}

.has-error {
    background-color: #ffc8c8 !important;
}

.dropdown-menu .dropdown-item:hover, .dropdown-menu .dropdown-item:focus, .dropdown-menu a:hover, .dropdown-menu a:focus, .dropdown-menu a:active{
    background-color : #00aec5;
    color:#fff !important;
    cursor: pointer;
}


