/* Multi Picture */
.choice-group input[type="checkbox"] {
    display: none;
}

.choice-group label {
    position: relative;
    padding: 5px 10px;
    background-color: transparent;
    border: thin solid #FFF;
    border-radius: 2px;
    margin: 5px;
    cursor: pointer;
    text-align: center;
    width: 90px;
}

.choice-group input[type="checkbox"]:checked+label {
    background-color: #eaeaea;
    cursor: pointer;
}

.choice-group input[type="checkbox"]:checked+label span {
    background-color: transparent;
    border: 1px solid #75A7D3;
    color: #75A7D3;
}

.choice-group input[type="checkbox"]:checked+label a {
    color: #75A7D3;
}

.choice-group {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.multi-choice label {
    color: #000;
}

.multi-choice label span {
    color: #000;
    cursor: pointer;
    background: #97BBDC;
    border: 1px solid #FFF;
    border-radius: 2px;
    font-size: 6px;
    height: 10px;
    padding: 0 2px;
    text-align: center;
    width: 10px;
    position: absolute;
    bottom: 10px;
    line-height: 1.2;
}

.multi-choice label a {
    margin-left: 16px;
    color: #000;
}

.multi-choice label img {
    margin-bottom: 8px;
    width: 100%;
}

.row-box:hover{
    background-color: #cecece;
    cursor: pointer;
}

