ul.tree,
ul.tree ul {
    list-style-type: none;
    background: url(https://www.cssscript.com/demo/pure-javascript-treeview-component-aimarajs/css/vline.png) repeat-y;
    margin: 0;
    padding: 0;
    padding-left: 7px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    cursor: default;
}

li.last {
    background-image: 'url(https://www.cssscript.com/demo/pure-javascript-treeview-component-aimarajs/css/css/lastnode.png)';
    background-repeat: no-repeat;
}

ul.tree ul {
    padding-left: 7px;
}

ul.tree li {
    margin: 5px 0 0;
    padding: 3px 12px;
    line-height: 22px;
    background: url(https://www.cssscript.com/demo/pure-javascript-treeview-component-aimarajs/css/node.png) no-repeat;
}

ul.tree li.last {
    background: #fff url(https://www.cssscript.com/demo/pure-javascript-treeview-component-aimarajs/css/lastnode.png) no-repeat;
}

img.exp_col {
    position: absolute;
    margin-top: 2px;
    margin-left: -20px;
    vertical-align: sub;
    cursor: pointer;
}

img.icon_tree {
    vertical-align: middle;
    padding-left: 3px;
    margin-top: -3px;
}

span.node a {
    padding-left: 3px;
}

span.node {
    margin-left: -1px;
    padding-right: 3px;
    padding-top: 4px;
    padding-bottom: 4px;
}

span.node:hover {
    margin-left: -1px;
    padding-right: 3px;
    padding-top: 4px;
    padding-bottom: 4px;
    background-color: #dcedff;
    border-radius: 2px;
}

span.node_selected {
    margin-left: -2px;
    padding-right: 3px;
    padding-top: 4px;
    padding-bottom: 4px;
    background-color: #ceffce;
    border: 1px solid #8ae88a;
    border-radius: 2px;
}

span.node_selected a {
    padding-left: 3px;
}

.links line {
    stroke: #999;
    stroke-opacity: 0.6;
  }
  
  .nodes circle {
    stroke: #fff;
    stroke-width: 1.5px;
  }


